import { Button, Icon, Modal, Tab, Tabs } from 'patternfly-react';
import React, { Component } from 'react';
import { getContentDetailsByCollectionTypeAndId, getContentDetailsById } from '../api/Api';
import { renderPanelGroup } from '../helper/AccordionHelper';

export default class ContentDetailModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show: true,
            collectionTypes: [],
            mockRows: [],
            selectedContent: [],
            selectedCollectionType: null,
            collectionAttributes: this.props.dummyData,
            activeTabKey: 0,
            dataToShowOnModal: {}
          };
        this.toggleTab = this.toggleTab.bind(this);
    }

    toggleTab = (index) => {
        this.setState({ activeTabKey: index })
    }

    componentDidUpdate = async (prevProps, prevState) => {
      if (this.props.collectionType && this.props.contentId) {
        if (prevProps.collectionType !== this.props.collectionType || prevProps.contentId !== this.props.contentId) {
          const contentDettailsResponse = await getContentDetailsByCollectionTypeAndId(this.props.collectionType, this.props.contentId);
          this.setState({ dataToShowOnModal: { ...contentDettailsResponse } });
        }
      }
    }

    render() {
        return (
            <>
                <Modal dialogClassName="ContentsFilterModal" show={this.props.show} onHide={this.props.onHide}>
                    <Modal.Header>
                        <button
                            className="close"
                            onClick={this.props.onHide}
                            aria-hidden="true"
                            aria-label="Close"
                        >
                            <Icon type="pf" name="close" />
                        </button>
                        <Modal.Title>{Object.keys(this.state.dataToShowOnModal).length > 0 && this.state.dataToShowOnModal[Object.keys(this.state.dataToShowOnModal)[1]]}
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div
                            className="CollapsibleSection__title no-padding"
                            role="button"
                            tabIndex={0}
                        >
                        </div>
                        <div>
                            <Tabs id={'id'} activeKey={this.state.activeTabKey} onSelect={this.toggleTab}>
                                 <Tab eventKey={0} title="English" style={{ overflowY: "auto", height: "38rem", overflowX: "hidden"}}>
                                    {renderPanelGroup(this.state.dataToShowOnModal)}
                                </Tab>
                                <Tab eventKey={1} title="Italiano" style={{ overflowY: "auto", height: "38rem", overflowX: "hidden"}}>
                                    {renderPanelGroup(this.state.dataToShowOnModal)}
                                </Tab>
                            </Tabs>
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button bsStyle={'default'} className="btn-cancel" onClick={this.props.onHide}>
                            Close
                        </Button>
                        <Button bsStyle={'primary'} onClick={this.close} onClick={this.props.onHide}>
                            Ok
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }
}
